import React, { useContext, useState } from "react";
import { useLocation } from "react-router-dom";
import "./style.css";
import { ContextStore } from "../../context/ContextStore";
const State = () => {
    // S dng mng d liu sn c
    // const date = new Date();
    const { actions, setActions } = useContext(ContextStore);

    const itemsPerPage = 10; // S lng dng trn mi trang
    const [currentPage, setCurrentPage] = useState(1);

    // Tnh ton d liu trang hin ti da trn currentPage v itemsPerPage
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = actions.slice(indexOfFirstItem, indexOfLastItem);

    // Hm chuyn trang
    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    return (
        <div>
            <h1>D liu bt tt n</h1>
            <table>
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Thit b</th>
                        <th>State</th>
                        <th>Thi gian</th>
                    </tr>
                </thead>
                <tbody>
                    {currentItems.map((item) => (
                        <tr key={item.id}>
                            <td>{item.id}</td>
                            <td>{item.device}</td>
                            <td>{item.state}</td>
                            <td>{item.time}</td>
                        </tr>
                    ))}
                </tbody>
            </table>

            {/* Phn trang */}
            <div className="pagination">
                {Array.from({
                    length: Math.ceil(actions.length / itemsPerPage),
                }).map((_, index) => (
                    <button
                        key={index}
                        onClick={() => paginate(index + 1)}
                        className={currentPage === index + 1 ? "active" : ""}
                    >
                        {index + 1}
                    </button>
                ))}
            </div>
        </div>
    );
};

export default State;
